@import nulstyle


body
	font-family: 'Montserrat', sans-serif
a
	color: #fff
.header
	background-image: url(../images/background.jpg)
	background-repeat: no-repeat
	background-size: cover
	background-position: center
	color: #fff
	height: 100%
.container
	max-width: 1140px
	width: 100%
	margin: 0 auto
	padding: 0 15px
.header__list
	display: flex
	justify-content: space-between
	padding: 40px 0
	position: absolute
	right: 0
.burder-list
	display: flex
	justify-content: space-between
	transition: .3s
.header-link
	padding-right: 40px
	font-weight: 500
	font-size: 16px
	line-height: 20px
.header-link:nth-child(5)
	padding-right: 0
.header__top
	position: relative
.top-line
	width: 100%
	height: 1px
	background-color: #A1A1A1
.burg-line
	width: 100%
	height: 1px
	background-color: #A1A1A1
	position: absolute
	display: none
	top: 99px
.top-line-2
	width: 100%
	height: 1px
	background-color: #E0E0E0
.box-top-line
	padding-top: 99px

.header-normal
	text-align: center
	padding-top: 220px
.header3-title
	font-weight: 500
	font-size: 16px
	line-height: 20px
.header1-title
	font-weight: 800
	font-size: 48px
	line-height: 64px
	padding-bottom: 50px
.header__button
	font-weight: 500
	font-size: 16px
	line-height: 20px
	background: #4985FF
	border-radius: 30px
	padding: 13px 36px 15px
	float: center


/*-=-==-=-=-==-=-=-*/


.block-1
	text-align: center
	padding-bottom: 105px
	padding-top: 110px
.block-1__columns
	display: flex
	justify-content: space-between
	text-align: left
	padding-bottom: 55px
.block-1__column
	display: flex
	padding-right: 30px
.block-1__column:nth-child(3)
	padding-right: 0
.block-1__column-list-title
	font-weight: 600
	font-size: 18px
	line-height: 22px
	color: #000000
	padding-bottom: 12px
.block-1__column-list-text
	font-weight: 400
	font-size: 14px
	line-height: 22px
	color: #999999
	max-width: 222px
	width: 100%
.block-list
	text-align: center
.title
	font-weight: 700
	font-size: 36px
	line-height: 53px
	color: #323232
	padding: 0px 0 50px 0
.text
	font-weight: 400
	font-size: 16px
	line-height: 25px
	color: #999999
	padding-bottom: 70px
.block-1__img
	padding-right: 27px
	margin-bottom: 50px
	max-width: 140px
	max-height: 120px
.block-1__column-list
	padding-top: 50px


/*-=-=-==-=-=-=-=-=-=-=-=-*/


.block-2
	background: #F6F6F8
.block-2__columns
	display: flex
	justify-content: space-between
	align-items: center
	padding: 100px 0
.block-2__text
	max-width: 400px
.block-2__projects
	display: flex
	justify-content: space-between
	padding: 70px 0
.block-2__project
	padding-right: 30px
.block-2__project:nth-child(5)
	padding-right: 0
.block-2__project-box
	display: flex
	align-items: center
.block-2__project-title
	font-weight: 400
	font-size: 36px
	line-height: 53px
	color: #000000
	padding-left: 20px
.block-2__img-2
	width: 30px
	height: 30px
.block-2__project-text
	font-weight: 400
	font-size: 16px
	line-height: 25px
	color: #999999

.block-2__img
	max-width: 520px
	width: 100%

/*-=-==-=-=-=-=-=-=-=-=-=-*/


.block-3
	background: linear-gradient(90deg, #504DC2 20.44%, #5F12D0 80.91%), #C4C4C4
	height: 490px
.block-3__body
	display: flex
	justify-content: space-between
	align-items: center
	padding-top: 100px
.block-3 .title
	color: #fff
.block-3-2
	padding-bottom: 85px
	margin-top: -220px
.box-top-line-2
	padding-top: 85px
.block-3__columns
	display: flex
	justify-content: space-between
.block-3__column
	padding-right: 30px
.block-3__column:nth-child(3)
	padding-right: 0
.block-3__column-list
	display: flex
	span
		font-weight: 600
		font-size: 18px
		line-height: 22px
		color: #4985FF
.block-3__column-text
	font-weight: 400
	font-size: 16px
	line-height: 25px
	color: #999999
.block-1__column-link
	font-weight: 500
	font-size: 16px
	line-height: 20px
	text-decoration-line: underline
	color: #4985FF
	cursor: pointer
	padding-top: 30px
.block-3-2-img
	margin-bottom: 30px
	max-width: 350px
	width: 100%


/*-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/


.block-4
	background: #F6F6F8
	padding: 100px 0 130px 0
.block-4__images
	display: flex
	justify-content: space-between
.block-4__img
	max-width: 255px
	width: 100%
	margin-right: 30px
.block-4__img:nth-child(4)
	margin-right: 0


/*-=-=-=-=-=-==-=-=-=--=-==--==-=-=-=-=-=-*/


.block-5
	background-image: url(../images/Rectangle.png)
	background-repeat: no-repeat
	background-size: cover
	background-position: center
	max-height: 570px
	text-align: center
.block-5 .title
	color: #FFFFFf
	padding-top: 70px
	padding-bottom: 30px
.block-5 .text
	color: #fff
	font-weight: 400
.block-5__images
	text-align: center
.block-5__img
	max-width: 970px
	width: 100%
	z-index: 10
.block-5-box
	display: flex
	align-items: center
	width: 300px
	border: 1px solid #FFFFFF
	border-radius: 200px
	padding: 20px 40px
	cursor: pointer
	transition: 0.3s
	margin-left: auto
	margin-right: auto
	margin-top: -250px
	z-index: 999
	&:hover
		background-color: #272727
		border: 1px solid #272727
.block-5-box__text
	font-weight: 500
	font-size: 16px
	line-height: 25px
	color: #FFFFFF
	padding-left: 21px

.block-5-lin
	padding: 200px 0 85px 0

.block-6
	padding-top: 250px
	text-align: center
.body-input
	display: flex
	max-width: 490px
	margin-left: auto
	margin-right: auto
.input
	font-weight: 400
	font-size: 13px
	line-height: 21px
	color: #999999
	border: 1px solid #F2F2F2
	box-sizing: border-box
	box-shadow: 0px 0px 10px rgba(111, 111, 111, 0.25)
	border-radius: 30px
	border: 1px solid #F2F2F2
	height: 45px
	width: 490px
	padding-left: 33px
	margin-bottom: 30px
.button
	font-weight: 500
	font-size: 16px
	line-height: 20px
	color: #FFFFFF
	background: #4985FF
	box-shadow: 0px 0px 10px rgba(111, 111, 111, 0.25)
	border-radius: 0px 30px 30px 0px
	height: 45px
	width: 190px
	margin-right: auto
	margin-left: -190px
.block-6__text
	font-weight: 400
	font-size: 13px
	line-height: 21px
	color: #999999
	padding-bottom: 85px
.block-6 .text
	padding-bottom: 30px


/*-=-=-=-=-=-=-=-=-=---=-*/


.footer
	background-color: #F6F6F8
	padding: 70px 0 30px 0
.footer-columns
	display: flex
	justify-content: space-around
.footer-box
	display: flex
	padding-top: 30px
	align-items: center
.ft-i
	padding-right: 15px
.footer__text
	font-weight: 400
	font-size: 14px
	line-height: 22px
	color: #999999
.footer-lin
	padding-top: 70px
.ft-text
	font-weight: 400
	font-size: 13px
	line-height: 21px
	text-align: center
	color: #999999
	padding-top: 30px



.checkbox
	display: none
.burger
	display: none



@media(max-width: 950px)
	.block-1__columns
		flex-wrap: wrap
		justify-content: space-around
	.block-1__column
		padding-bottom: 30px
	.block-1__column:nth-child(2)
		padding-right: 0
	.block-1__column:nth-child(3)
		padding-bottom: 0
@media(max-width: 840px)
	.footer-column
		padding-right: 20px
		padding-bottom: 40px
	.footer-columns
		flex-wrap: wrap
		text-align: left
	.block-3__columns
		flex-wrap: wrap
	.block-3__columns .block-3__column-text
		max-width: 350px
	.block-3__column:nth-child(2)
		padding-right: 0
	.block-3__column
		padding-bottom: 30px
		margin-right: auto
		margin-left: auto
	.block-4__images
		flex-wrap: wrap
		justify-content: space-around
	.block-4__img
		margin-right: 0
		padding-bottom: 20px
@media(max-width: 720px)
	.header1-title
		font-size: 40px
	.header
		height: 1000px
	.title
		font-size: 30px
	.block-2__columns
		flex-direction: column
	.block-2__projects
		flex-wrap: wrap
		justify-content: space-around
	.block-2__project
		padding-right: 0
		padding: 0 30px 30px 0
	.block-2__project:nth-child(5)
		padding-right: 30px
	.block-3__column
		padding-right: 0
	.block-5-box
		margin-top: -200px
@media(max-width: 680px)
	.block-5-box
		margin-top: 0
	.block-5__img
		margin-bottom: -200px
	.block-3__body
		flex-direction: column
	.block-3-2
		margin-top: -100px
@media(max-width: 600px)
	.header__list
		display: none

	.burger
		display: block
		position: absolute
		top: 25px
		margin: auto
		left: 0
		right: 0
		border-radius: 10px
		border: 1px solid #fff
		padding: 10px 20px
		width: 80px
	.burger-line:nth-child(2)
		margin: 8px 0
	.burger-line
		display: block
		height: 2px
		width: 40px
		border-radius: 50px
		background-color: #fff

	.block-6
		margin-top: 50px
	.block-5__img
		padding-top: 0px
	.block-5
		max-height: 370px
	.block-5-box
		margin-top: 20px
	.input
		height: 45px
		width: 320px
		margin-left: 100px
	.button
		width: 120px
		font-size: 14px
		margin-left: 10px
		margin-left: -120px
	.checkbox:checked ~ .header__list
		display: block
		width: 360px
		position: absolute
		top: 70px
		left: 0
		right: 0
		margin: auto
	.checkbox:checked ~ .burg-line
		top: 200px
	.burder-list
		padding-bottom: 30px
	.link-b
		padding-right: 0
	.burg-line
		display: block
	.header .box-top-line
		display: none
@media(max-width: 480px)
	.block-5-box
		display: none
	.input
		height: 45px
		width: 320px
		margin-left: 50px
	.button
		width: 120px
		font-size: 14px
		margin-left: 10px
		margin-left: -120px
	.burder-list
		max-width: 290px
		margin-right: auto
		margin-left: auto
	.header-link
		font-size: 15px
@media(max-width: 400px)
	.title
		font-size: 26px
	.block-5-box
		display: none
	.input
		height: 45px
		width: 320px
		margin-left: 0px
	.button
		width: 120px
		font-size: 14px
		margin-left: 10px
		margin-left: -120px